import React, { Component } from 'react';
import {Navbar,Sidebar} from './Header';
import './css/docs.min.css';
import './css/custom.css';


class Agents extends Component {
    render() {
      return (
        <div>
          <div className="d-flex justify-content-start"><Sidebar/></div>
            <div className="content">  
            <Navbar/>
              <div className="p-3">
                <div class="d-flex">
                  <div class="mr-auto p-2"><h4 className="d-flex justify-content-start">Operators</h4></div>
                  <div class="p-2"><button type="button" className="btn btn-outline-primary">ADD NEW</button></div>                  
                </div>
                <div className="row p-2">
                <CardOperators/>
                <CardOperators/>
                <CardOperators/>
                <CardOperators/>
                <CardOperators/>
                <CardOperators/>
                </div>
              </div>   
          </div> 
        </div>
      );
    }
  }
class CardOperators extends Component {
  constructor(){
    super();
    this.state={
      "Name":"",
      "Email":"",
      "Phone":"",
      "GoodReview":0,
      "BadReview":0
    }
  }
    render() {
      return (
        <div className="col-sm-4 mb-4">
          <div>
                <div className="card">
                  <div className="card-body">
                  <div className="d-flex justify-content-end">
                  <button type="button" className="btn"><i class="fas fa-pen"></i></button>
                  </div>
                  <div className="d-flex justify-content-center">
                  <img src={require('./picUser.svg')} alt="..." className="rounded-circle " width="100px" height="100px"/> 
                  </div>
                  <form>
                    <div class="form-group">
                      <label>Name</label>
                      <input type="text" class="form-control border p-2" placeholder="" />                
                    </div>
                    <div class="form-group">
                      <label>Email</label>
                      <input type="text" class="form-control border p-2" placeholder="" />                
                    </div>
                    <div class="form-group">
                      <label>Phone</label>
                      <input type="text" class="form-control border p-2" placeholder="" />                
                    </div>
                    <div class="d-flex">
                    <div class="mr-auto p-2">                    
                      <div className="d-flex justify-content-end">
                        <button type="button" className="btn" id="GoodReview">
                        <i class="far fa-thumbs-up fa-3x"></i>
                        <p style={{"font-size":"10px","margin-top":"10px"}}>{this.state.GoodReview} GOOD REVIEWS</p> 
                        </button>
                      </div>                   
                    </div>
                    <div class="p-2">
                      <div className="d-flex justify-content-end">
                        <button type="button" className="btn" id="BadReview">
                        <i class="far fa-thumbs-down fa-3x"></i>
                        <p style={{"font-size":"10px","margin-top":"10px"}}>{this.state.BadReview} BAD REVIEWS</p> 
                        </button>
                      </div>
                    
                    </div>
                    </div>
                   
                    </form>
                  </div>
                </div>
          </div>
              
        </div>
      );
    }
}
export {Agents};


