import React, { Component } from 'react';
import './css/docs.min.css';
import './css/sidebar.css';
import {Link} from 'react-router-dom';
class Navbar extends Component {
    constructor(){
      super();
      this.state={
        "userName":"user",
        "picture":require('./picUser.svg')
      }
    }
    render() {
      return (
        <div>  
          <nav className="navbar navbar-expand navbar-dark bg-primary flex-column flex-md-row bd-navbar">
            <div className="container-fluid">
              <div className="navbar-header">
                <a className="navbar-brand" href="#"></a>
              </div>
              <ul className="nav">
               {/* <li className="nav-item text-center"><i className="far fa-bell"></i></li> */}
                <li className="nav-item">                
                  <img src={this.state.picture} alt="..." className="rounded-circle" width="40px" height="40px"/>               
                </li>
                <li className="nav-item">
                  <span className="nav-link" href="#" style={{"color": "white","fontSize":"10px"}}>Welcome back,<br/>
                  {this.state.userName}</span>              
                </li>             
              </ul>
              </div>
          </nav>
          
        </div>
      );
    }
  }

  class Sidebar extends Component {
 
    render() {
      
      return (
        // <div style={{"color":"white"}} className="bg-dark col-sm-2 col-md-2">
          
        //     <div className="list-group" style={{"color":"red"}}>
        //           <a className="list-group-item list-group-item-action" href="#list-item-1">Dashboard</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-2">Plans</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-3">Offers</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-4">Requests</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-4">Operator (Contact)</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-4">Chat</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-4">Your profile</a>
        //           <a className="list-group-item list-group-item-action" href="#list-item-4">Log Out</a>             
        //     </div>     
             
        // </div>
        <div class="nav-side-menu">
        <div class="brand">APP LOGO</div>
       
        <div class="menu-list">
            <ul id="menu-content" class="menu-content collapse out">
                <Link to="/"><li class="active"><a href="#">Dashboard</a></li></Link>        
                <Link to="/plans"><li><a href="/plans">Plans</a></li></Link>
                <Link to="/"><li><a href="#">Offers</a></li></Link>  
                <Link to="/"><li><a href="#">Requests</a></li></Link> 
                <Link to="/agents"><li><a href="#">Operator (Contact)</a></li></Link>
                <Link to="/"><li><a href="#">Chat</a></li></Link> 
                <Link to="/profile"><li><a href="#">Your profile</a></li></Link>
                <Link to="/"><li><a href="#">Log Out</a></li></Link> 
       
            </ul>
        </div>
    </div>

      );
    }
  }

export {Navbar,Sidebar};



