import React, { Component } from 'react';
// import './css/bootstrap-material-design.css';
import {Navbar,Sidebar} from './Header';
import './css/docs.min.css';
import './css/custom.css';
class Plans extends Component {
    render() {
      return (
        <div>              
            <div className="d-flex justify-content-start"><Sidebar/></div>
            <div className="content">            
              <Navbar/>
              <div className="p-3">
                <h4>Plans</h4>
                <ul className="nav nav-tabs" id="Plans" role="tablist">
                    <li className="nav-item">
                      <a className="nav-link active" id="basic-tab" data-toggle="tab" href="#PlanBasic" role="tab" aria-controls="PlanBasic" aria-selected="true">BASIC</a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link" id="premium-tab" data-toggle="tab" href="#PlanPremium" role="tab" aria-controls="PlanPremium" aria-selected="false">PREMIUM</a>
                    </li>                          
                </ul>
                <div className="tab-content">
                  <div className="tab-pane fade show active" id="PlanBasic" role="tabpanel" aria-labelledby="basic-tab"><PlanBasic/></div>
                  <div className="tab-pane fade show" id="PlanPremium" role="tabpanel" aria-labelledby="premium-tab"><PlanPremium/></div>
                </div>
                          
                </div>   
            </div>              
        </div>
      );
    }
  }
  class PlanBasic extends Component {
    render() {
      return (
        <div>
          <div className="card">
            <div className="card-body">
                <form>
                  <div className="form-group">
                      <div className="row">
                        <div className="col-sm-2 col-md-2 align-self-center">
                          <div className="custom-control custom-checkbox ">
                            <input type="checkbox" className="custom-control-input" id="CheckEnableChatBasic"/>
                            <label className="custom-control-label" for="CheckEnableChatBasic">Enable Chat</label>
                          </div>
                        </div>                      
                        <div className="col-sm-2 col-md-2 ">
                          <label for="From">From</label>
                          <input type="text" className="form-control border p-2" id="From" placeholder="Select Date"/>
                        </div>
                        <div className="col-sm-2 col-md-2 ">
                        <label for="To">To</label>
                          <input type="text" className="form-control border p-2" id="To" placeholder="Select Date"/>
                        </div>
                      </div>
                    </div>
                  <div className="form-group">                      
                      <div className="custom-control custom-checkbox">
                        <input type="checkbox" className="custom-control-input" id="CheckEnableHistoryBasic"/>
                        <label className="custom-control-label" for="CheckEnableHistoryBasic">Enable History</label>
                      </div>
                  </div>
                  <button type="button" className="btn btn-outline-primary col-sm-2 col-md-2">Update</button>
                </form>
              </div>                    
          </div>
        </div>
      );
    }
  }
  class PlanPremium extends Component {
    render() {
      return (
        <div>
          <div className="card">
            <div className="card-body">
                <form>
                  <div className="form-group">
                      
                          <div className="custom-control custom-checkbox ">
                            <input type="checkbox" className="custom-control-input" id="CheckEnableChatPremium"/>
                            <label className="custom-control-label" for="CheckEnableChatPremium">Enable Chat</label>
                          </div>
                                             
                       
                    </div>
                  <div className="form-group">                      
                      <div className="custom-control custom-checkbox">
                        <input type="checkbox" className="custom-control-input" id="CheckEnableHistoryPremium"/>
                        <label className="custom-control-label" for="CheckEnableHistoryPremium">Enable History</label>
                      </div>
                  </div>
                  <button type="button" className="btn btn-outline-primary col-sm-2 col-md-2">Update</button>
                </form>
              </div>                    
          </div>
        </div>
      );
    }
  }
export {Plans};


