import React, { Component } from 'react';
import {Navbar,Sidebar} from './Header';
import './css/docs.min.css';
import './css/custom.css';

class Profile extends Component {
    render() {
      return (
        <div>
          <div className="d-flex justify-content-start"><Sidebar/></div>
            <div className="content">  
            <Navbar/>
              <div className="p-4">
                <div class="d-flex">
                  <div class="mr-auto p-2"><h4 className="d-flex justify-content-start">Your profile</h4></div>                                
                </div>
                
                    <div className="card">
                        <div className="card-body">
                            <Form/>
                        </div>
                    </div>
                
              </div>   
          </div> 
        </div>
      );
    }
  }
  class Form extends Component{
    constructor(){
        super();
        this.state={
          "Name":"",
          "Email":"",
          "Password":""
          
        }
      }
      render(){
          return(
            <div>
                <form className="col-sm-6 col-md-6 p-2">
                    <div class="form-group">
                      <label>Name</label>
                      <input type="text" class="form-control border p-2" placeholder=""/>                
                    </div>
                    <div class="form-group">
                      <label>Email</label>
                      <input type="text" class="form-control border p-2" placeholder=""/>                
                    </div>
                    <div class="form-group">
                      <label>Password</label>
                      <input type="password" class="form-control border p-2" placeholder="" />                
                    </div>
                    <button type="button" className="btn btn-outline-primary col-sm-2 col-md-2">Update</button>
                </form>
            </div>
          );
      }
  }
  export {Profile};


